import {
    MAP_INIT,
    CHANGED_MAP_SOURCE,
    MAPPING_INIT,
} from '../constants/Map';

export function initMap(source,width,height) {

    console.log('INIT MAP',width,height);
    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: {
                source,
                width,
                height,
            }
        });

    }
}

export function changedMapSource(source) {

    console.log('VI POPALIS` !!!!', source);
    return (dispatch) => {
        dispatch({
            type: CHANGED_MAP_SOURCE,
            payload: source
        });

    }
}

export function initMapping(mapping) {

    return (dispatch) => {
        dispatch({
            type: MAPPING_INIT,
            payload: mapping
        });

    }
}
